import React, { useEffect, useState, useCallback } from 'react'
import gsap from 'gsap'
import { Route, useLocation } from 'react-router-dom'
import { useWindowSize } from './hooks/hooks'
import Menu from './components/Menu/Menu'
import Controls from './components/Controls/Controls'
import Hero from './pages/Hero/Hero'
import Works from './pages/Works/Works'
import Blog from './pages/Blog/Blog'
import Contacts from './pages/Contacts/Contacts'
import Cursor from './components/Cursor/Cursor'

import './App.scss'

function App() {
  const windowSize = useWindowSize()
  const location = useLocation()
  const [wasAnimated, setWasAnimated] = useState(false)
  const [isHover, setIsHover] = useState(false)
  const mouseoverHandler = useCallback(() => setIsHover(true), [])
  const mouseleaveHandler = useCallback(() => setIsHover(false), [])

  useEffect(() => {
    gsap.to('body', { css: { visibility: 'visible' } })
  }, [])

  useEffect(() => {
    if (location.pathname === '/' && !wasAnimated) {
      const tl = gsap.timeline({delay: 1.5, defaults: {ease: 'expo.inOut'}, onComplete: () => setWasAnimated(true)})

      tl.from('.cv-link__top', { opacity: 0, x: -2000, duration: 2.8})
        .from('.cv-link__bottom', { opacity: 0, x: 2000, duration: 2.8}, '<')
        .from('.cv-link__left', { opacity: 0, y: 2000, duration: 2.8}, '<')
        .from('.cv-link__right', { opacity: 0, y: -2000, duration: 2.8}, '<')
        .from('.cv-link__content', { opacity: 0, duration: .8})
        .from('.info__title span', { opacity: 0, y: 100, duration: 0.8, stagger: 0.2})
        .fromTo('.menu .nav-button', { y: '-100%' }, { y: 0, duration: 1})
        .fromTo('.theme-switcher', { x: 20, opacity: 0 }, { x: 0, opacity: 1, duration: 1}, '<')

    }
  }, [location.pathname, wasAnimated])

  return (
    <div className="App">
      <Menu mouseoverHandler={mouseoverHandler} mouseleaveHandler={mouseleaveHandler} />
      <Controls mouseoverHandler={mouseoverHandler} mouseleaveHandler={mouseleaveHandler} />
      <main>
        <Route path='/' exact>
          <Hero mouseoverHandler={mouseoverHandler} mouseleaveHandler={mouseleaveHandler} />
        </Route>
        <Route path='/works'>
          <Works mouseoverHandler={mouseoverHandler} mouseleaveHandler={mouseleaveHandler} />
        </Route>
        <Route path='/blog'>
          <Blog mouseoverHandler={mouseoverHandler} mouseleaveHandler={mouseleaveHandler} />
        </Route>
        <Route path='/contacts'>
          <Contacts mouseoverHandler={mouseoverHandler} mouseleaveHandler={mouseleaveHandler} />
        </Route>
      </main>
      {windowSize >= 992 ? <Cursor isHover={isHover} /> : null}
    </div>
  );
}

export default App
